* {
	box-sizing: border-box;
	margin: 0px;
    padding: 0px;
}

@font-face {
    font-family: myFirstFont;
    src: url(../media/calibril.ttf);
  }

body{
    font-family: myFirstFont;
    overflow-x: hidden;
    overflow-y: hidden;
}


.desktop{
    z-index: 1;

    background-image: url('../media/bg.jpg');
    background-size: cover;
    width: 100vw;
    height: 100vh;
    color: white;
}

.iconGrid{
    z-index: 2;

    justify-content: start;
    position: absolute;
    display: grid;
    grid-gap: 5px;
    grid-template-columns: repeat(auto-fit, 65px);
    grid-template-rows: repeat(auto-fit, 75px);

    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 35px;

}

.activateWindows{
    position: absolute;
    right: 60px;
    bottom: 50px;
    color: white;
    opacity: 0.5;
    .top{
        font-size: 24px;
    }
    .down{
        font-size: 16px;
    }
}
